<template>
  <div class="alarm-management">
    <div class="secondary-toolbar">
      <span>时间选择 : </span>
      <div class="layoutBox" style="width:242px">
        <el-date-picker v-model="dateRange" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
      </div>&emsp;&emsp;
      <span>报警类型 : </span>
      <div class="layoutBox" style="width:120px">
        <el-select v-model="typeSelect"  collapse-tags placeholder="请选择">
          <el-option
            v-for="item in optionData"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>&emsp;&emsp;
      <span>持续时长 : </span>
      <div class="layoutBox" style="width:100px">
        <el-input-number v-model="duration" controls-position="right" :min="0"></el-input-number>
      </div> 分钟以上&emsp;&emsp;
      <el-button><i class="iconfont icon-sousuo"></i>查询</el-button>
      <el-button type='delet'><i class="iconfont icon-daochu"></i>导出</el-button>
    </div>
    <ul class="alarm-statistics-list clear">
      <li>
        <div class="alarm-info">
          <p class="title">待巡检</p>
          <p class="content-text"><i class="icon-baojing iconfont"></i> {{ useTime }}个报警</p>
        </div>
      </li>
      <li>
        <div class="alarm-info">
          <p class="title">本周站点平均日巡检次数</p>
          <p class="content-text"><i class="iconfont icon-dongtaiguankong"></i> {{ taskNumber }}个</p>
        </div>
      </li>
      <li>
        <div class="alarm-info">
          <p class="title">现行日巡检规定次数：2次</p>
          <p class="content-text"><i class="iconfont icon-dongtaiguankong"></i> 2次</p>
        </div>
        <span class="setting" @click="visiDialogC = true">修改设置 > > </span>
      </li>
    </ul>
    <div class="alarm-modular modular-box">
      <div class="modular-head">
        <span class="title">报警列表</span>
        <div class="modular-toolbar">
          <div class="state-radio-wrap">
            <el-radio-group v-model="stateRadio" size="small">
              <el-radio-button label="1">全部</el-radio-button>
              <el-radio-button label="2">正常</el-radio-button>
              <el-radio-button label="3">异常</el-radio-button>
            </el-radio-group>
          </div>
          <div class="search-box" style="width: 164px;">
            <i class="iconfont icon-sousuo"></i>
            <input type="text" class="search-input" placeholder="请输入"/>
          </div>
        </div>
      </div>
      <div class="modular-body">
         <el-scrollbar style="height: 100%">
           <ul class="alert-list">
             <li v-for="(item,index) in alarmList" :key="index">
               <el-row>
                 <el-col :span="8">
                   <p><i class="site-icon"></i><span class="site-name">{{ item.siteName }}</span></p>
                   <p class="content-text"><span>站点状态 : {{ item.contentText }}</span></p>
                 </el-col>
                 <el-col :span="4">
                   <p><span class="label-text">运维</span></p>
                   <p><span>{{ item.operation }}</span></p>
                 </el-col>
                 <el-col :span="4">
                   <p><span class="label-text">正常(ycxj20190101-01)</span></p>
                   <p><span>{{ item.startTime }}</span></p>
                 </el-col>
                 <el-col :span="4">
                   <p><span class="label-text">正常(ycxj20190101-01)</span></p>
                   <p><span>{{ item.startTime }}</span></p>
                 </el-col>
                 <el-col :span="4" style="text-align:center">
                   <el-button type="secondBtn" @click="visiDialogA =true">远程巡检</el-button>
                   <el-button type="secondBtn" @click="visiDialogB =true">巡检历史</el-button>
                 </el-col>
               </el-row>
               <el-row>
                 <el-col :span="24">
                   <div class="label-state-wrap">
                     <span class="label-state" style="margin-right:15px"> 实时工况 </span>
                    <span class="label-state state-a" v-if="item.stateType === 1"> 正常 </span>
                     <span class="label-state state-b" v-if="item.stateType === 2"> 报警 </span>
                      <span class="label-state state-c" v-if="item.stateType === 3"> 掉线 </span>
                   </div>
                 </el-col>
               </el-row>
             </li>
           </ul>
         </el-scrollbar>
      </div>
    </div>
    <el-dialog :visible.sync="visiDialogB" width="1075px" title="巡检历史" :before-close="closess">
         <div>
              <div class="toolbar">
                   <span>时间选择 : </span>
                   <div class="layoutBox" style="width:242px">
                     <el-date-picker v-model="dateRange" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
                   </div>&emsp;&emsp;
                  
                   <div class="layoutBox" style="width:164px;vertical-align: 1px;">
                        <el-input placeholder="请输入关键词" v-model="value"></el-input>
                   </div>&emsp;&emsp;
                   <el-button><i class="iconfont icon-sousuo"></i>查询</el-button>
              </div>
              <div class="tablebox" style="margin-top:12px;">
                   <el-table border :data="tableData" height="420px">
                          <el-table-column
                            type="index"
                            label="序号"
                            :index="indexMethod"
                            width="50">
                          </el-table-column>
                          <el-table-column
                            prop="code"
                            label="表单号">
                          </el-table-column>
                          <el-table-column
                            prop="date"
                            width="165px"
                            label="时间">
                          </el-table-column>
                          <el-table-column
                            prop="station"
                            label="站点">
                          </el-table-column>
                          <el-table-column
                            prop="isYx"
                            label="数据采集情况及有效性判定">
                          </el-table-column>
                          <el-table-column
                            prop="yStatus"
                            label="仪器情况">
                          </el-table-column>
                          <el-table-column
                            prop="xStatus"
                            width="128px"
                            label="系统运行情况">
                          </el-table-column>
                          <el-table-column
                            prop="tStatus"
                            label="通讯情况">
                          </el-table-column>
                          <el-table-column
                            prop="remark"
                            label="备注">
                          </el-table-column>
                          <el-table-column
                            prop="file"
                            label="附件下载">
                            <template>
                              <div>
                                  <el-button type="secondBtn">下载</el-button>
                              </div>
                            </template>
                          </el-table-column>
                   </el-table>
              </div>
              <div class="table-pagination-wrap">
                <el-pagination
                  @size-change="paginationSizeChange"
                  @current-change="paginationChange"
                  :current-page.sync="currentPage"
                  :page-size="pageSize"
                  popper-class="select-pagination-option"
                  background
                  :small="true"
                  :page-sizes="pageList"
                  layout="total, sizes, slot,->,prev, pager, next, jumper"
                  :total="total">
                  <template v-slot:default>
                    <span class="el-pagination__current">当前第{{ currentPage }}/{{totalPageNumber}}页</span>
                  </template>
                </el-pagination>
              </div>
         </div>
         
    </el-dialog>
    <el-dialog :visible.sync="visiDialogA" width="722px" title="远程巡检" :before-close="closess">
       <div class="dialogcontent">
             <div class="dialog-layout">
                    <ul class="formul">
                      <li>
                          <div>
                                <span class="layoutspan">表单号 :</span>&emsp;
                                <div class="layoutBox input_code" style="width:218px">
                                    <el-input v-model="input"  placeholder="系统自动生成，可以编辑"></el-input>
                                </div>
                          </div>
                          <div>
                                <span class="layoutspan">通讯情况 :</span>&emsp;
                                <div class="layoutBox" style="width:218px">
                                      <el-radio v-model="input" label="1">正常</el-radio>
                                      <el-radio v-model="input" label="2">离线</el-radio>
                                </div>
                          </div>
                      </li>
                      <li>
                          <div>
                                <span class="layoutspan">时间 :  </span>&emsp;
                                <div class="layoutBox" style="width:218px">
                                  <el-input v-model="input" ></el-input>
                                </div>
                          </div>
                          <div>
                               <span class="layoutspan" >站点 :</span>&emsp;
                                <div class="layoutBox" style="width:140px;">
                                   <el-select v-model="typeSelect"  collapse-tags placeholder="请选择">
                                      <el-option
                                        v-for="item in optionData"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                      </el-option>
                                    </el-select>
                                </div>
                                <div class="layoutBox" style="width:60px;vertical-align: -2px;">
                                     <span class="ssBtn">实时监控</span>
                                </div>
                          </div>
                      </li>
                      <li>
                          <div>
                                <span class="layoutspan" style="line-height: 15px;">数据采集情况及有效性判定 :</span>&emsp;
                                <div class="layoutBox" style="width:218px">
                                
                                  <el-input v-model="input"  placeholder="系统自动生成，可以编辑"></el-input>
                                </div>
                          </div>
                          <div>
                               <span class="layoutspan">仪器情况 :</span>&emsp;
                                <div class="layoutBox input_code" style="width:218px">
                                  <el-input v-model="input"  placeholder="系统自动生成，可以编辑"></el-input>
                                </div>
                          </div>
                      </li>
                     
                      <li>
                          <div style="width:100%">
                                <span class="layoutspan">系统运行情况 :</span>&emsp;
                                <div class="layoutBox" style="width:554px">
                                  <el-input v-model="input" type="textarea" :rows="3" placeholder="系统自动生成，可以编辑"></el-input>
                                </div>
                          </div>
                      </li>
                       <li>
                          <div style="width:100%">
                                <span class="layoutspan">备注 :</span>&emsp;
                                <div class="layoutBox" style="width:554px">
                                  <el-input v-model="input" type="textarea" :rows="3" placeholder="请输入200字以内的介绍"></el-input>
                                </div>
                          </div>
                      </li>
                      <li>
                          <span class="layoutspan">资质上传 : </span>&emsp;
                                <el-upload
                                  class="upload-demo"
                                  ref="upload"
                                  action="https://jsonplaceholder.typicode.com/posts/"
                                  :on-preview="handlePreview"
                                  :on-remove="handleRemove"
                                  :file-list="fileList"
                                  :auto-upload="false">
                                  <span slot="trigger" class="upload_span_sc"><i class="icon-shangchuan1 iconfont"></i> 上传</span>
                                 
                                </el-upload>
                      </li>
                    </ul>
             </div>
       </div>
       <div slot="footer" class="dialog-footer">
          <el-button type="noticon" @click="visiDialog = false">确 定</el-button>
          <el-button type="noticonsecondBtn" @click="visiDialog = false">取 消</el-button>
       </div>
    </el-dialog>
    <el-dialog :visible.sync="visiDialogC" width="367px" title="修改设置" :before-close="closess">
       <div class="dialogcontent">
           <div class="dialog-layout">
                <ul class="formul">
                      <li>
                          <div style="width:100%">
                                <span class="layoutspan">日巡检次数 :</span>&emsp;
                                <div class="layoutBox input_code" style="width:155px">
                                     <el-input-number v-model="duration" controls-position="right" :min="0"></el-input-number>
                                </div>
                          </div>
                      </li>
                       <li>
                          <div style="width:100%">
                                <span class="layoutspan">是否开启 :</span>&emsp;
                                <div class="layoutBox input_code" style="width:155px;text-align: left;">
                                    <el-switch
                                      v-model="value"
                                      width="50px"
                                      active-text="否"
                                      inactive-text="是">
                                    </el-switch>
                                </div>
                          </div>
                      </li>
                    </ul>
           </div>
       </div>
        <div slot="footer" class="dialog-footer">
          <el-button type="noticon" @click="visiDialogC = false">提 交</el-button>
          <el-button type="noticonsecondBtn" @click="visiDialogC = false">取 消</el-button>
       </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "alarmManage",
  data() {
    return {
      dateRange: "",
      typeSelect: "",
      optionData: [],
      duration: "",
      value:'',
      useTime: "8", //待巡检
      taskNumber: "2", //本周站点平均日巡检次数
      alarmNumber: "8", //待处理报警数
      stateRadio: 1,
      alarmList: [
        {
          id: "1",
          siteName: "生米",
          contentText: "橘子洲站高锰酸钾盐还原液试剂报警",
          operation: "吴加好（宇星）",
          startTime: "2016-06-16 14:03",
          usedTime: "00:45:00",
          usedProgress: 70,
          alarmType: "仪器传输",
          stateType: 1,
          handleState: 1
        },
        {
          id: "2",
          siteName: "生米",
          contentText: "橘子洲站高锰酸钾盐还原液试剂报警",
          operation: "吴加好（宇星）",
          startTime: "2016-06-16 14:03",
          usedTime: "00:45:00",
          usedProgress: 30,
          alarmType: "仪器传输",
          stateType: 2,
          handleState: 0
        },
        {
          id: "1",
          siteName: "生米",
          contentText: "橘子洲站高锰酸钾盐还原液试剂报警",
          operation: "吴加好（宇星）",
          startTime: "2016-06-16 14:03",
          usedTime: "00:45:00",
          usedProgress: 70,
          alarmType: "仪器传输",
          stateType: 3,
          handleState: 2
        },
        {
          id: "1",
          siteName: "生米",
          contentText: "橘子洲站高锰酸钾盐还原液试剂报警",
          operation: "吴加好（宇星）",
          startTime: "2016-06-16 14:03",
          usedTime: "00:45:00",
          usedProgress: 70,
          alarmType: "仪器传输",
          stateType: 1,
          handleState: 1
        },
        {
          id: "2",
          siteName: "生米",
          contentText: "橘子洲站高锰酸钾盐还原液试剂报警",
          operation: "吴加好（宇星）",
          startTime: "2016-06-16 14:03",
          usedTime: "00:45:00",
          usedProgress: 30,
          alarmType: "仪器传输",
          stateType: 2,
          handleState: 0
        },
        {
          id: "1",
          siteName: "生米",
          contentText: "橘子洲站高锰酸钾盐还原液试剂报警",
          operation: "吴加好（宇星）",
          startTime: "2016-06-16 14:03",
          usedTime: "00:45:00",
          usedProgress: 70,
          alarmType: "仪器传输",
          stateType: 1,
          handleState: 2
        }
      ],
      visiDialogA:false,
      visiDialogB:false,//巡检历史弹框
      visiDialogC:false,
      tableData:[{code:'XZUS215',
                  date:'2018-9-10 10:10:10',
                  station:'生米',
                  isYx:'有效',
                  yStatus:'运行',
                  xStatus:'运行',
                  tStatus:'正常',
                  remark:'数据问题',
      }],
      currentPage: 1, //分页组件当前页
      total: 32, //分页总条数
      pageSize: 10, //分页每页多少条数据
      pageList: [10, 15, 20], //设置每页多少条
      input:'',
      fileList:[],
    };
  },
  created() {
    this.$store.commit("setShowlefttree", true);
  },
  methods:{
    indexMethod(index) {
      return index + 1;
    },
     paginationChange(val) {
      //分页页码改变时执行
      this.currentPage = val;
    },
    paginationSizeChange(val) {
      //分页pageSize改变时执行
      this.currentPage = 1;
      this.pageSize = val;
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
  },
   computed: {
    totalPageNumber() {
      //计算总页数
      let number = 0;
      number = Math.ceil(this.total / this.pageSize);
      return number;
    }
  }
};
</script>

<style scoped lang="less">
.alarm-management {
  height: 100%;
  
  .alarm-statistics-list {
    margin-top: 6px;
    li {
      float: left;
      border: 1px solid #d9e6f7;
      width: calc(33.33% - 4px);
      margin-right: 6px;
      text-align: center;
      position: relative;
      &:last-child {
        margin-right: 0;
      }
      .alarm-info {
        display: inline-block;
        padding: 16px 0;
        text-align: left;
        .title {
          color: #666666;
          line-height: 24px;
          margin-bottom: 4px;
        }
        .content-text {
          font-size: 22px;
          line-height: 30px;
          i {
            color: #166dce;
          }
          .iconfont {
            font-size: 22px;
          }
        }
      }
      .setting {
        position: absolute;
        right: 6px;
        bottom: 6px;
        color: #666666;
        font-size: 12px;
        cursor: pointer;
      }
    }
  }
  .alarm-modular {
    height: calc(100% - 155px);
    margin-top: 6px;
    .modular-toolbar {
      float: right;
    }
  }
}
.modular-box {
  border: 1px solid #dce9fa;
  .modular-head {
    padding: 7px 12px;
    border-bottom: 1px solid #dce9fa;
    line-height: 30px;
    .title {
      font-size: 16px;
    }
    .title:before {
      content: "";
      display: inline-block;
      width: 3px;
      height: 10px;
      background: #166bce;
      margin: 0 6px 1px 0;
    }
  }
  .modular-body {
    height: calc(100% - 45px);
    .alert-list {
      padding: 0 16px;
      & > li {
        padding: 20px 0;
        border-bottom: 1px solid #dce9fa;
        .el-col {
          line-height: 26px;
          color: #666666;
          padding-right: 32px;
          position: relative;
          &:first-child {
            padding-left: 28px;
          }
        }
        .site-icon {
          position: absolute;
          width: 20px;
          height: 29px;
          background: url("../../assets/images/state-icon.png") no-repeat;
          left: 0;
          top: 0;
        }
        .content-text {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
        }
        .site-name {
          font-size: 16px;
          color: #333;
          font-weight: 700;
        }
        .label-text {
          color: #999999;
        }
        .progress-wrap {
          padding-top: 10px;
        }
        .label-state {
          display: inline-block;
          vertical-align: top;
          width: 70px;
          height: 24px;
          line-height: 22px;
          text-align: center;
          border: 1px solid #166bce;
          border-radius: 12px 0;
          font-size: 12px;
          color: #166bce;
        }
        .label-state-wrap {
          padding-top: 8px;
        }
        .state-a {
          color: #36c25e;
          border-color: #36c25e;
        }
        .state-b {
          color: #ff3333;
          border-color: #ff3333;
        }
        .state-c {
          color: #ff7e00;
          border-color: #ff7e00;
        }
      }
      & > li:last-child {
        border-bottom: none;
      }
    }
  }
}
.state-radio-wrap {
  display: inline-block;
  vertical-align: top;
}
.search-box {
  display: inline-block;
  vertical-align: top;
  position: relative;

  height: 30px;
  line-height: 30px;
  .icon-sousuo {
    position: absolute;
    right: 10px;
    top: 0;
    color: #7c8fa4;
    cursor: pointer;
    font-size: 14px;
    z-index: 2;
  }
  .icon-sousuo:hover {
    color: #166bce;
  }
  .icon-sousuo:active {
    color: #1056a7;
  }
  .search-input {
    position: absolute;
    box-sizing: border-box;
    width: 100%;
    height: 30px;
    top: 0;
    left: 0;
    padding-left: 5px;
    padding-right: 32px;
    outline: none;
    border: 1px solid #c6d1de;
  }
}
.layoutspan{
  width: 95px;
}
.layoutBox{
  .ssBtn{
    display: block;
    width: 72px;
    height: 30px;
    line-height: 30px;
    text-align: center;
    border: 1px solid #166bce;
    color: #166bce;
    cursor: pointer;
    &:hover{
      background: #166bce;
      color: #fff;
    }
  }
}
.formul{
  li{
    text-align: center;
  }
}
</style>



